<template>
    <div>
        <b-button @click="showDelProposalConditionSearchModal" variant="primary"  >{{ $t("DELETEPROPOSALCONDITIONSEARCH") }}</b-button>

        <!-- 削除提案条件検索 -->
        <b-modal 
            id="delProposalConditionSearch-modal"
            ref="delProposalConditionSearch-modal"
            centered
            variant="secondary"
            modal-class="py-0"
            body-class="py-0"
            size="xl"
            footer-class="border-0"
            header-class="border-0
            bg-white">
            <template v-slot:modal-header="{ cancel }">
                <div class="title-fw title-fs text-dark">{{ $t("DELETEPROPOSALCONDITIONSEARCH") }} </div>
                <div class="d-flex justify-content-end">
                    <button @click="cancel()" class="btn-sm border-dark text-dark font-weight-bold text-sm m-0 bg-white" >X </button>
                </div>
            </template>
            <div class="my-0 py-0 px-2">
                <b-row>
                    <b-col cols="12" xl="1" class="pr-0">
                        <label class="highLightHeaderLabel">{{ $t("REGISTRATIONFEE") }}</label>&nbsp;&nbsp;
                    </b-col>
                    <b-col cols="12" xl="2" class="pl-0">
                        <b-form-select :plain="true" :options="expenseItemOptions" value="トナー代" ></b-form-select>
                    </b-col>
                </b-row>
                <div class="border border-dark pl-4 pr-4">
                    <b-table
                        ref="delProposalConditionSearchTable"
                        :fields="delProposalConditionSearchFields"
                        :items="delProposalConditionSearchTableData"
                        select-mode="single"
                    >
                        <template v-slot:cell(selectRow)="{ rowSelected }">
                            <template v-if="rowSelected">
                                <input class="form-checkbox-input" type="checkbox" checked />
                            </template>
                            <template v-else>
                                <input class="form-checkbox-input" type="checkbox" />
                            </template>
                        </template>
                        <template v-slot:cell(Item1)="item">{{ item.value }}</template>
                        <template v-slot:cell(Item2)="item">{{ item.value }}</template>
                        <template v-slot:cell(Item3)="item">{{ item.value }}</template>
                        <template v-slot:cell(Item4)="item">{{ item.value }}</template>
                        <template v-slot:cell(Item5)="item">{{ item.value }}</template>
                    </b-table>
                </div>
                <b-row class="mt-2">
                    <b-col xl="4"></b-col>
                    <b-col xl="6">
                        <label class="title-fw title-fs text-dark">
                            {{ $t("DELETEPROPOSALCONDITIONSEARCHMEMO")}}
                        </label>
                    </b-col>
                    <b-col xl="2">
                        <b-button variant="primary">{{ $t("SEARCH") }}</b-button>
                    </b-col>
                </b-row>
            </div>
            <template v-slot:modal-footer="{ cancel }">
                <div class="w-100">
                    <button @click="cancel()" class="btn-sm px-1 float-left btn-primary" >{{ $t("CLOSE") }} </button>
                </div>
            </template>
        </b-modal>
    </div>
</template>

<script>
export default {
    
    data() {
        return {
            delProposalConditionSearchTableData: [],
            expenseItemOptions: [
                "固定電話",
                "ドコモ携帯",
                "電気代",
                "会食代",
                "トナー代"
            ]
        }
    },

    computed: {
        delProposalConditionSearchFields() {
            
            return [
                {
                    key: "selectRow",
                    label: "",
                    class: "text-center normal-width"
                },
                {
                    key: "Item1",
                    label: this.$t("EXPENSEITEM"),
                    class: "text-center normal-width"
                },
                {
                    key: "Item2",
                    label: this.$t("REDUCTIONPROPOSALMENUNAME"),
                    class: "text-center normal-width"
                },
                {
                    key: "Item3",
                    label: this.$t("EXPIRATIONDATESTART"),
                    class: "text-center normal-width"
                },
                {
                    key: "Item4",
                    label: this.$t("EXPIRATIONDATEEND"),
                    class: "text-center normal-width"
                },
                {
                    key: "Item5",
                    label: this.$t("PROPOSEDAMOUNT"),
                    class: "text-center normal-width"
                }
            ];
        }
    },

    mounted() {
        this.getDelProposalConditionSearchTableData();
    },

    methods: {
        showDelProposalConditionSearchModal() {
            this.$refs["delProposalConditionSearch-modal"].show();
        },
        getDelProposalConditionSearchTableData() {
                this.delProposalConditionSearchTableData = [
                    {
                        Item1: "トナー代",
                        Item2: "トナー代削減提案",
                        Item3: "2020/09/01",
                        Item4: "2020/09/01",
                        Item5: "120,000",
                    },
                    {
                        Item1: "トナー代",
                        Item2: "トナー代削減提案",
                        Item3: "2020/09/01",
                        Item4: "2020/09/01",
                        Item5: "120,000",
                    },
                    {
                        Item1: "トナー代",
                        Item2: "トナー代削減提案",
                        Item3: "2020/09/01",
                        Item4: "2020/09/01",
                        Item5: "120,000",
                    }

                ];
                this.delProposalConditionSearchTableData.forEach((element, index) => {
                    if (index === this.delProposalConditionSearchTableData.length - 1) {
                    this.delProposalConditionSearchTableData[index].last_element = true;
                    return;
                    }
                });
            },  
    }
}
</script>

<style>

    .highLightHeaderLabel {
        font-weight: 700 !important;
        padding-top: 5px;
    }

</style>